
import XHeader from 'vux/src/components/x-header/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Badge from 'vux/src/components/badge/index.vue'

import { mapGetters } from "vuex";

import scroll from "../scroll";
import api from "../../api/index";
import axios from "axios";
export default {
components: {
XHeader,
Swiper,
SwiperItem,
Grid,
GridItem,
Badge,
scroll

},
data() {
return {
pullDownRefresh: true,
pullDownRefreshThreshold: 90,
pullDownRefreshStop: 40,
pullDownRefreshText: "",
pullUpLoad: true,
pullUpLoadThreshold: 0,
pullUpLoadMoreTxt: "",
pullUpLoadNoMoreTxt: "",
startY: 0,
scrollToX: 0,
scrollToY: -200,
scrollToTime: 700,
scrollToEasing: "bounce",
scrollToEasingOptions: ["bounce", "swipe", "swipeBounce"],
userid: "",
page: 1,
num: 10,



banner_list: [],
banner_index: 0,
homeNews: [],
tipNums: 0,
registrationid: ""
};
},

computed: {
...mapGetters([
"homeList",
"homeListTotal",
"errorAnNewsMsg",
"homeNewsScrollTop",
"unNewsNums",
"banners"
]),
pullDownRefreshObj: function() {
return this.pullDownRefresh
? {
threshold: parseInt(this.pullDownRefreshThreshold),
stop: parseInt(this.pullDownRefreshStop),
text: ""
}
: false;
},
pullUpLoadObj: function() {
return this.pullUpLoad
? {
threshold: parseInt(this.pullUpLoadThreshold),
txt: {
more: this.pullUpLoadMoreTxt,
noMore: this.pullUpLoadNoMoreTxt
}
}
: false;
}
},
created() {},

activated() {
console.log("init...");
document.addEventListener("deviceready", this.onDeviceReady, false);
document.addEventListener(
"jpush.receiveMessage",
this.onReceiveMessage,
false
);

this.$store.dispatch("getBanners");
this.userid = window.localStorage.getItem("user");
this.$store.dispatch("getHomeList", { user_id: this.userid });
this.$store.dispatch("getUnNewsTotals", { user_id: this.userid });
},
mounted() {




},

watch: {
errorAnNewsMsg: function(value) {
this.$vux.toast.text(value, "middle");
},
pullDownRefreshObj: {
handler(val) {
const scroll = this.$refs.scroll.scroll;
if (val) {
scroll.openPullDown();
} else {
scroll.closePullDown();
}
},
deep: true
},
pullUpLoadObj: {
handler(val) {
const scroll = this.$refs.scroll.scroll;
if (val) {
scroll.openPullUp();
} else {
scroll.closePullUp();
}
},
deep: true
},
startY() {
this.rebuildScroll();
},













homeList: function(val) {
this.homeNews = val;
},
banners: function(val) {
this.banner_list = val;
},
unNewsNums: function(val) {
this.tipNums = val;
}
},
methods: {
scrollTo() {
this.$refs.scroll.scrollTo(
this.scrollToX,
this.scrollToY,
this.scrollToTime,
ease[this.scrollToEasing]
);
},
onPullingDown() {
console.log("pulling down and load data");
setTimeout(() => {
if (this._isDestroyed) {
return;
}

this.$store.dispatch("getBanners");
this.$store.dispatch("getHomeList", { user_id: this.userid });
this.$store.dispatch("getUnNewsTotals", { user_id: this.userid });
this.$refs.scroll.forceUpdate();
this.page = 1;
}, 2000);
},
onPullingUp() {
console.log("pulling up and load data");
setTimeout(() => {
if (this._isDestroyed) {
return;
}
this.page++;
this.$store
.dispatch("getHomeList", { user_id: this.userid, page: this.page })
.then(() => {
if (this.homeNews.length >= this.homeListTotal) {
this.$refs.scroll.forceUpdate();
}
});
}, 1500);
},

rebuildScroll() {
Vue.nextTick(() => {
this.$refs.scroll.destroy();
this.$refs.scroll.initScroll();
});
},




banner_onIndexChange(index) {
this.banner_index = index;
},



























onDeviceReady() {
try {
console.log("device ready ...");
window.JPush.init();
window.JPush.setDebugMode(true);
window.setTimeout(this.getRegistrationID, 1000);




console.log("jpush init ok...");
} catch (exception) {
console.log(exception);
}
},
getRegistrationID() {
console.log("jpush get registrationid ...");
console.log(window.JPush);

window.JPush.getRegistrationID(this.onGetRegistrationID);
console.log("jpush get registrationid 2...");
},
onGetRegistrationID(data) {
console.log("jpush get registrationid callback ...");
try {
console.log("JPushPlugin:registrationID is " + data);

console.log(data, typeof data);
console.log(data.length);

if (data.length == 0) {
var t1 = window.setTimeout(this.getRegistrationID, 1000);
} else {
this.registrationid = data;

var url = "http://rugao.ztemap.com:8086/api/jpush/push/register";
var data = { register_id: data, user_id: this.userid };

fetch(url, {
method: "POST", // or 'PUT'
body: JSON.stringify(data), // data can be `string` or {object}!
headers: {
"Content-Type": "application/json"
}
})
.then(res => res.json())
.catch(error => console.error("Error:", error))
.then(response => console.log("Success:", response));
}
} catch (exception) {
console.log(exception);
}
},
onReceiveMessage(event) {
try {
var message;
if (device.platform == "Android") {
message = event.message;
} else {
message = event.content;
}


this.tipNums = JSON.parse(message).count;

} catch (exception) {
console.log("JPushPlugin:onReceiveMessage-->" + exception);
}
}
}
};



